.enter__reset {
  position: absolute;
  bottom: 15px;
  left: 12px;
  font-size: 16px;
  padding: 7px 10px 7px 10px;
  border-radius: 5px;
  border: 2px solid #2F89FC;
  background: transparent;
  cursor: pointer;
  outline: none;
  color: #fff;
}

.enter__reset:hover {
  opacity: 0.7;
}